@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
.page {
  background-color: #ffffff;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: #000000;
}

.page__body {
  margin: 0 auto;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main-header__logo {
  height: 65px;
  width: 200px;
}

.main-header {
  position: relative;
}

.main-header__wrapper {
  width: 280px;
  padding: 10px 20px;
  margin: 0 auto;
  position: relative;
}

.main-header__logo-mobile {
  min-height: 38px;
}
.main-header__logo-mobile:hover {
  opacity: 0.8;
}
.main-header__logo-mobile:active {
  opacity: 0.6;
}

@media (min-width: 320px) {
  .main-header__wrapper {
    padding-bottom: 13px;
  }

  .main-header__toogle--opened {
    display: block;
  }

  .main-header__toogle--nojs {
    display: none;
  }

  .main-header__toogle {
    position: absolute;
    cursor: pointer;
    border: 0;
    width: 20px;
    height: 20px;
    top: 22px;
    right: 22px;
    background-color: transparent;
  }

  .main-header__toogle--closed::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 24px;
    height: 18px;
    background-image: url("../img/navigation-button.svg");
  }

  .main-header__toogle--opened::before {
    content: "";
    position: absolute;
    top: 1px;
    right: 1px;
    width: 20px;
    height: 20px;
    background-image: url("../img/navigation-closed.svg");
  }
}
@media (min-width: 768px) {
  .main-header__wrapper {
    width: 708px;
    padding: 24px 30px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .main-header__toogle--closed::before {
    display: none;
  }

  .main-header__toogle--opened::before {
    display: none;
  }

  .main-header__logo {
    height: 75px;
    display: block;
  }

  .main-header__logo-mobile {
    min-height: 52px;
    width: 174px;
  }
}
@media (min-width: 1440px) {
  .main-header {
    position: relative;
  }

  .main-header__wrapper {
    width: 1220px;
    padding: 53px 110px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .main-header__logo-mobile {
    min-height: 59px;
    width: 202px;
  }

  .main-header__toogle--closed::before {
    display: none;
  }

  .main-header__toogle--opened::before {
    display: none;
  }

  .main-header__promo {
    position: relative;
    background-image: linear-gradient(to bottom, rgba(104, 183, 56, 0.85) 720px, #ffffff 720px), url("../img/background-image-desktop.jpg");
    background-repeat: no-repeat;
    background-size: 50%;
    background-position: right top;
    min-height: 694px;
  }
}
