.advantages {
  width: 280px;
  padding: 20px 20px 26px;
  margin: 0 auto;
}

.advantages__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
  margin: 0 auto;
  padding-bottom: 40px;
}

.advantages__item {
  position: relative;
  display: block;
}
.advantages__item::after {
  position: absolute;
  content: "";
  width: 60px;
  height: 60px;
  background-color: #68b738;
  z-index: 1;
  top: 0;
}

.advantages__item:nth-child(1)::after {
  background-image: url("../img/icon_leaf.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.advantages__item:nth-child(2)::after {
  background-image: url("../img/icon_tub.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.advantages__item:nth-child(3)::after {
  background-image: url("../img/icon_eat.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.advantages__item:nth-child(4)::after {
  background-image: url("../img/icon_alarm.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.advantages__text {
  margin: 0 auto;
  text-align: start;
  padding-bottom: 26px;
  padding-left: 80px;
}

.advantages__list {
  margin: 0;
  padding: 0;
