    width: 708px;
    padding: 0 30px;
    margin: 0 auto;
  }

  .advantages__title {
    font-size: 60px;
    line-height: 60px;
    padding-top: 45px;
    padding-bottom: 72px;
  }

  .advantages__item {
    position: relative;
    display: block;
    padding-bottom: 20px;
  }
  .advantages__item::after {
    position: absolute;
    content: "";
    width: 80px;
    height: 80px;
    background-color: #68b738;
    z-index: 1;
    top: 0;
  }
  .advantages__item::before {
    content: "";
    position: absolute;
    top: -39px;
    left: 113px;
    z-index: -1;
    font-family: "Oswald", "Arial", sans-serif;
    font-size: 281px;
    line-height: 281px;
    color: #f2f2f2;
  }

  .advantages__item:nth-child(1)::before {
    content: "1";
  }

  .advantages__item:nth-child(2)::before {
    content: "2";
  }

  .advantages__item:nth-child(3)::before {
    content: "3";
  }

  .advantages__item:nth-child(4)::before {
    content: "4";
  }

  .advantages__list {
    display: grid;
    grid-template-columns: 246px 246px;
    column-gap: 138px;
    row-gap: 88px;
    margin-bottom: 54px;
  }

  .advantages__text {
    font-size: 16px;
    line-height: 24px;
    padding-top: 111px;
    padding-left: 0;
  }
}
@media (min-width: 1440px) {
